<template>
  <div class="main-collection critical-component-clear">
    <template
      v-if="isSearchPage"
    >
      <div class="main-collection__search-tools">
        <div class="container">
          <breadcrumb
            :page-title="$string('collection.general.breadcrumb_search')"
          />

          <h1
            class="visually-hidden"
            v-text="$string('search.title')"
          />
          <search-bar
            :predictive="false"
            :default-value="searchQuery"
            @query-update="updateQuery"
          />
          <div class="main-collection__results-count">
            <span
              v-if="!resultCount"
              v-text="$string(
                'search.no_results_predictive',
                { replace: { query: searchQuery } },
              )"
            />
            <span
              v-else-if="resultCount === 1"
              v-text="$string(
                'search.stats.search_singular',
                { replace: { query: searchQuery, count: resultCount } },
              )"
            />
            <span
              v-else
              v-text="$string(
                'search.stats.search_plural',
                { replace: { query: searchQuery, count: resultCount } },
              )"
            />
          </div>
        </div>
      </div>

      <div
        v-if="resultCount"
        class="container"
      >
        <ul class="main-collection__accordion">
          <li class="main-collection__accordion-item">
            <button
              class="main-collection__accordion-button"
              :class="{
                'is-active': activeSection === 'products'
              }"
              @click="activeSection = 'products'"
              v-text="$string('search.products')"
            />
          </li>
          <li>
            <button
              class="main-collection__accordion-button"
              :class="{
                'is-active': activeSection === 'collections'
              }"
              @click="activeSection = 'collections'"
              v-text="$string('search.collections')"
            />
          </li>
          <li>
            <button
              class="main-collection__accordion-button"
              :class="{
                'is-active': activeSection === 'pages'
              }"
              @click="activeSection = 'pages'"
              v-text="$string('search.pages')"
            />
          </li>
        </ul>
      </div>
    </template>

    <div v-show="isCollectionPage || resultCount">
      <ais-instant-search
        v-show="activeSection === 'products'"
        :class-names="{
          'ais-InstantSearch': 'container',
        }"
        :index-name="`${indexName}products`"
        :search-client="searchClient"
        :insights="true"
      >
        <ais-configure
          v-bind="searchParams"
        />

        <div class="main-collection__info">
          <ais-stats v-if="isCollectionPage">
            <template #default="{ nbHits }">
              <div
                class="main-collection__count"
                v-text="filterStatsText(nbHits)"
              />
            </template>
          </ais-stats>
          <div class="main-collection__sort">
            <ais-relevant-sort />
            <ais-sort-by
              :items="getCollectionSortOrders"
            />
          </div>
        </div>
        <div class="main-collection__grid">
          <div class="main-collection__filter-toggle">
            <button
              class="button button--primary button--block"
              data-target="filterMenu"
              js-toggle="filterMenu"
              @click.prevent="toggleOverlay({
                ignoreDismissed: true,
                namespace: 'filterDrawer',
              })"
            >
              <span v-text="$string('collection.general.filter_label')" />
              <icon-arrow-up-right />
            </button>
          </div>

          <overlay
            v-if="isMobile"
            key="filterDrawer"
            block-class="filter-drawer-overlay"
            direction="down"
            namespace="filterDrawer"
            :template="false"
            type="drawer"
          >
            <template #body>
              <div
                class="main-collection__sidebar drawer drawer--bottom is-active"
              >
                <main-collection-facets :facets="facets" />
              </div>
            </template>
          </overlay>
          <div
            v-else
            class="main-collection__sidebar drawer drawer--bottom"
          >
            <main-collection-facets :facets="facets" />
          </div>

          <div class="main-collection__body">
            <ais-hits
              :transform-items="transformItems"
            >
              <template
                #default="{items}"
              >
                <ol class="card-grid">
                  <template
                    v-for="(item, index) in items"
                    :key="item.sku"
                  >
                    <template v-if="hasMatchingBanner(index)">
                      <li
                        v-for="banner in getMatchingBanner(index)"
                        :key="`banner-${banner.location}`"
                        class="card-grid__item"
                      >
                        <inline-banner
                          :banner="banner"
                        />
                      </li>
                    </template>
                    <li class="card-grid__item">
                      <product-card
                        :is-algolia="true"
                        :collection="liquid"
                        :product="item"
                        :index="index"
                        @click="saveAlgoliaTrackingData(item)"
                      />
                    </li>
                    <li
                      v-if="getOverflowBanner(items, index)"
                      class="card-grid__item"
                    >
                      <inline-banner
                        :banner="getOverflowBanner(items, index)"
                      />
                    </li>
                  </template>
                </ol>
              </template>
            </ais-hits>
          </div>
        </div>
      </ais-instant-search>

      <template v-if="isSearchPage">
        <ais-instant-search
          v-show="activeSection === 'collections'"
          :class-names="{
            'ais-InstantSearch': 'container',
          }"
          :index-name="`${indexName}collections`"
          :search-client="searchClient"
          :insights="true"
        >
          <ais-configure v-bind="searchParams" />

          <div class="main-collection__collection-grid container">
            <ais-hits>
              <template #item="{ item, sendEvent }">
                <collection-card
                  :collection="item"
                  @click="sendEvent('conversion', item)"
                />
              </template>
            </ais-hits>
          </div>
        </ais-instant-search>

        <ais-instant-search
          v-show="activeSection === 'pages'"
          :class-names="{
            'ais-InstantSearch': 'container',
          }"
          :index-name="`${indexName}pages`"
          :search-client="searchClient"
          :insights="true"
        >
          <ais-configure v-bind="searchParams" />

          <div class="main-collection__page-grid container">
            <ais-hits>
              <template #item="{ item }">
                <page-card
                  :title="item.title"
                  :handle="item.handle"
                />
              </template>
            </ais-hits>
          </div>
        </ais-instant-search>
      </template>
    </div>
  </div>
</template>
<script>

/**
 * Vue: Main collection (main-collection)
 * -----------------------------------------------------------------------------
 * main collection.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
import { AisConfigure, AisHits, AisInstantSearch, AisRelevantSort, AisSortBy, AisStats } from 'vue-instantsearch/vue3/es'
import { mapActions, mapGetters, mapState } from 'vuex'
import { nextTick } from 'vue'

import { saveForConversionTracking, getUserToken } from '~/helpers/algolia'
import algoliasearch from 'algoliasearch/lite'
import Breadcrumb from '~global/breadcrumb/breadcrumb'
import ProductCard from '~async/product-card/product-card'
import CollectionCard from '~async/collection-card/collection-card'
import PageCard from '~async/page-card/page-card'
import MainCollectionFacets from './main-collection-facets'
import SearchBar from '~global/search-bar/search-bar'
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'
import InlineBanner from './inline-banner'

const {
  app_id,
  collection_facets,
  collection_id_indexing,
  collection_sort_orders,
  index_prefix,
  search_api_key,
} = window.algoliaShopify.config
const { id: collectionId } = window.cnvs.collection

export default {
  name: 'MainCollection',

  components: {
    AisConfigure,
    AisHits,
    AisInstantSearch,
    AisRelevantSort,
    AisSortBy,
    AisStats,
    IconArrowUpRight,
    InlineBanner,
    MainCollectionFacets,
    ProductCard,
    CollectionCard,
    PageCard,
    Breadcrumb,
    SearchBar,
  },

  props: {
    collection: {
      type: Object,
      default: () => ({}),
    },
    liquid: {
      type: Object,
      default: () => ({
        sectionId: '',
      }),
    },
    banners: {
      type: Array,
      default: () => ([]),
    },
  },

  setup() {
    const loadedBanners = 0
    const loaded = []

    return {
      loaded,
      loadedBanners,
    }
  },

  data() {
    return {
      collectionId,
      drawerMenuOpen: false,
      indexName: index_prefix,
      isCollectionPage: cnvs.page.type === 'collection',
      isSearchPage: cnvs.page.type === 'search',
      searchClient: algoliasearch(
        app_id,
        search_api_key,
      ),
      searchQuery: null,
      activeSection: 'products',
      resultCount: 0,
      assignedBanners: [],
      totalBanner: 0,
      queryID: null,
      queryIndex: null,
      reviewsLoadAttempts: 0,
    }
  },

  computed: {

    /**
     * Map Vuex getters.
     */
    ...mapGetters({
      getActiveOverlay: 'overlays/getActiveOverlay',
    }),

    /**
     * Map Vuex state.
     */
    ...mapState({
      isMobile: (state) => state.index.screen.width <= 768,
      settings: (state) => state.index.settings,
    }),

    /**
     * Get all collection facets.
     */
    facets() {
      const collectionFacets = collection_facets[this.collectionId]?.filter((facet) => facet.searchable && facet.enabled)
      return collectionFacets
    },

    /**
     * Set sort orders from config.
     */
    getCollectionSortOrders() {
      const sortOrderBase = `${index_prefix}products`
      let orders = null
      const sortOrder = collection_sort_orders[this.collectionId]

      orders = [
        {
          value: sortOrderBase,
          label: 'Relevance',
        },
      ]

      sortOrder.forEach((order) => {
        if (order.asc && (order.asc.active === true || order.asc.active === '1')) {
          orders.push({
            value: `${sortOrderBase}_${order.key}_asc`,
            label: order.asc.title,
          })
        }

        if (order.desc && (order.desc.active === true || order.desc.active === '1')) {
          orders.push({
            value: `${sortOrderBase}_${order.key}_desc`,
            label: order.desc.title,
          })
        }
      })

      return orders
    },

    /**
     * Get Algolia Search Parameters
     */
    searchParams() {
      if (this.isSearchPage) {
        return this.searchPageParams
      }

      return this.collectionPageParams
    },

    /**
     * Get Collection Page Search Parameters
     */
    collectionPageParams() {
      if (collection_id_indexing) {
        return {
          filters: `collections:${this.collection.handle}`,
          ruleContexts: [this.collection.handle],
          hitsPerPage: 1000,
          facetingAfterDistinct: true,
          clickAnalytics: true,
        }
      }

      return {
        filters: this.collection.id ? `collection_ids:"${this.collection.id}"` : null,
        facetingAfterDistinct: true,
        clickAnalytics: true,
      }
    },

    /**
     * Get Search Page Search Parameters
     */
    searchPageParams() {
      if (!this.searchQuery) {
        return
      }

      return {
        query: this.searchQuery,
        facetingAfterDistinct: true,
        clickAnalytics: true,
      }
    },
  },

  mounted() {
    this.getAssignedBanners()
  },

  created() {
    this.searchQuery = new URLSearchParams(window.location.search).get('q')
  },

  methods: {

    /**
     * Map Vuex actions.
     */
    ...mapActions({
      closeOverlay: 'overlays/close',
      openOverlay: 'overlays/open',
      toggleOverlay: 'overlays/toggle',
    }),

    transformItems(items, { results }) {
      this.queryID = results.queryID
      this.queryIndex = results.index
      this.resultCount = items.length

      nextTick(() => {
        this.resetReviews()
      })

      return items.map((item) => ({
        ...item,
        compare_at_price: Number(item.compare_at_price )*100,
        price: Number(item.price )*100,
      }))


    },

    filterStatsText(hits) {
      return hits > 1 || hits === 0
        ? this.$string('search.stats.plural').replace('{{ count }}', hits)
        : this.$string('search.stats.singular').replace('{{ count }}', hits)
    },

    updateQuery(query) {
      this.searchQuery = query

      const params = new URLSearchParams(window.location.search)
      params.set('q', query)

      const url = `${location.origin}${location.pathname}?${params.toString()}`

      history.replaceState({
        html: '',
      }, '', url)
    },

    getAssignedBanners() {
      const banners = this.banners.filter(banner => banner.collection === this.collection.handle)
      if (!banners) {
        return
      }

      const sortedBanner = banners.sort((banner1, banner2) => {
        return banner1.location - banner2.location
      })

      this.assignedBanners = sortedBanner
    },

    hasMatchingBanner(index) {
      if (this.assignedBanners.length <= 0) {
        return
      }

      if (index === 0) {
        this.loadedBanners = 0
        this.loaded = []
      }
      const location = index + 1 + this.loadedBanners
      const matchingBanner = this.assignedBanners.find((banner) => banner.location === location )

      return matchingBanner
    },

    getMatchingBanner(index) {
      let totalLoaded = index === 0 ? 0 : this.loaded.length
      const location = 1 + totalLoaded
      let matchingBanners = []
      let count = index

      this.assignedBanners.forEach((banner) => {
        if (banner.location !== count + location) {
          return
        }

        matchingBanners = [...matchingBanners, banner]
        count ++
      })

      if (matchingBanners.length > 0) {
        totalLoaded += matchingBanners.length
      }

      this.loadedBanners = totalLoaded
      this.loaded = [...this.loaded, ...matchingBanners]

      return matchingBanners.length > 0 ? matchingBanners : false
    },

    getOverflowBanner(items, index) {
      const totalItems = items.length - 1

      if (index < totalItems || this.assignedBanners.length <= 0) {
        return
      }

      const matchingLocation = this.assignedBanners.find((banner) => {
        if (banner.location > items.length) {
          return banner
        }
        return false
      })

      return matchingLocation

    },

    /**
     * Reset yotpo star reviews
     * - recursively makes a few attempts
     */
    resetReviews() {
      if (window.yotpoWidgetsContainer &&
      typeof window.yotpoWidgetsContainer.initWidgets === 'function') {
        this.reviewsLoadAttempts = 0
        window.yotpoWidgetsContainer?.initWidgets()
        return
      }

      if (this.reviewsLoadAttempts < 10) {
        this.reviewsLoadAttempts++
        setTimeout(() =>{
          this.resetReviews()
        }, 300)
        return
      }

      this.reviewsLoadAttempts = 0
    },

    saveAlgoliaTrackingData(item) {
      const clickData = {
        eventName: 'click',
        index: this.queryIndex,
        objectIDs: [item.objectID],
        queryID: this.queryID,
        userToken: getUserToken(),
      }

      saveForConversionTracking(clickData)
    },
  },
}
</script>

<style lang="scss">
@import '~/../styles/components/card-grid';
@import '~/../styles/components/hero';
@import './main-collection';
</style>